.navbar{
    background: rgb(30, 30, 30);
    height: 5rem;
    padding: 0rem 2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: sticky;
    top: 0;
    z-index: 3;

}

.item{
    color: white;
    cursor: pointer;
}

.logo{
    object-fit: cover;
    height: 3rem;
    width: 10rem;
}

.desktopMenuListItem.active {
    color: yellow;
} 

.desktopMenu button{
    background: none;
    border: none;
    color: white;
    cursor: pointer;
    font-size: 1rem;
    margin: 0 1rem;

}

.desktopMenuListItem{
    margin: 1rem;
    cursor: pointer;
    color: white;
}

.desktopMenuListItem:hover{
    color: yellow;
    padding-bottom: 0.5rem;
    border-bottom: 3px solid yellow;
}
.desktopMenuListItem:active{
    color: yellow;
}

.desktopMenuBtn{
    background: white;
    color: black;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 5px 1rem;
    border-radius: 2rem;
}

.desktopMenuImg{
    width: 18px;
    height: auto;

}
.desktopMenu{
    object-fit: cover;
    height: 2rem;
    margin: 1rem;
}

.active{
    color: yellow;
}

.mobMenu{
    display: none;
    object-fit: cover;
    height: 1.8rem;
}

.navMenu{
    position: absolute;
    top: 4rem;
    right: 2rem;
    z-index: 2;
    display: flex;
    flex-direction: column;
    padding: 0.5rem;
    height: fit-content;
    min-width: 15rem;
    background: rgb(40,40,40);
    border-radius: 1rem;
}



.contact-btn{
    border-radius: 5rem;
}

.contactMeButton{
    border-radius: 5rem;
    outline: 0;
    border: 0;
    padding: 5px 1rem;
}
.listItem{
    padding: 1rem;
    border: 0;
    border-radius: 5px;
    /* width: 25rem; */
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    text-decoration: none;
    vertical-align: middle;
    outline: none;
    transition: background-color 0.3s cubic-bezier(0.8, 0.01, 0.25, 1);

}

.listItem:hover{
    background-color: #000000;
    cursor: pointer;
}

.contactMeButton span{
    /* padding: 4rem 0rem; */
}
.contactMeButtonMob{
    display: none;
    padding: 1rem;
    height: 2rem;
    color: black;

}

.contact-me-button{
    border-radius: 1rem;
    outline: none;
    border: none;
}
@media screen and (max-width: 1200px){
    .mobMenu{
        display: flex;
    }
    .desktopMenu{
        display: none;
    }
    .contactMeButtonDesk{
        display: none;
    }
    .contactMeButtonMob{
        display: flex;
    }
    .desktopMenuBtn{
        display: none;
    }

    
}
